.preview-img {
  filter: drop-shadow(9px 4px 5px #7e7d7d);
  width: 280px;
  height: 280px;

  object-fit: contain;
}

.item-images-image {

  filter: drop-shadow(6px 5px 4px #5f5c5c);
}



.product-details {
  font-size: 0.8rem;
}

.item-images-inside {
  width: 33%;
  height: 100%;
  overflow: 99%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;

  .image-box:hover {

    border: 1px dotted black;


  }
}

.item-images {


  display: none;

  .image-box:hover {

    border: 1px dotted black;


  }
}



@media only screen and (min-width: 600px) {
  .product-details {
    font-size: 0.8rem;
  }

  .item-images-inside {
    display: none;

    .image-box:hover {

      border: 1px dotted black;


    }
  }

  .item-images {
    // border: 1px solid black;
    width: 99%;
    height: 110px;
    overflow: hidden;
    display: flex;
 
    .image-box {
      height: 99%;
     


    }
  }

  .preview-img {
    filter: drop-shadow(9px 4px 5px #7e7d7d);
    width: 300px;
    height: 300px;


  }
}

@media only screen and (min-width: 768px) {
  .product-details {
    font-size: 0.8rem;
  }

  .preview-img {
    width: 300px;
    height: 300px;


  }
}

@media only screen and (min-width: 810px) {
  .item-images {

    width: 99%;
    height: 28%;
    overflow: hidden;
    display: flex;
  }

  .item-details {

    width: 99%;
    height: 60%;
  }

  .product-details {
    font-size: 0.8rem;
  }
}

@media only screen and (min-width: 1024px) {
  .product-details {
    font-size: 0.9rem;
    padding-left: 0.2rem;
  }

  .item-images {

    width: 99%;
    height: 30%;
    overflow: hidden;
    display: flex;
  }

  .item-details {

    width: 99%;
    height: 65%;
  }

 
}

@media only screen and (min-width: 1280px) {
  .item-images {

    width: 99%;
    height: 30%;
    overflow: hidden;
    display: flex;
  }

  .item-details {

    width: 99%;
    height: 60%;
  }

  .product-details {
    font-size: 0.9rem;
  }
}