.hover-link {
    width: 99%;
    color: rgb(63, 61, 61);
  

}

.hover-link:hover {

    color: black;


}

.price-label {
    height: 25%;
    width: 55%;
    border: 1px solid #7890a3;
 
    .price-input {
        height: 99%;
        width: 99%;
    }
}



@media only screen and (min-width: 600px) {
    .price-label {
        height: 25%;
        width: 45%;
       


    }



}

@media only screen and (min-width: 768px) {
    .price-label {
        height: 25%;
        width: 76%;
  


    }



}

@media only screen and (min-width: 810px) {
    .price-label {
        height: 25%;
        width: 73%;
        

    }



}

@media only screen and (min-width: 1024px) {
    .price-label {
        height: 25%;
        width: 77%;
     


    }



}

@media only screen and (min-width: 1280px) {
    .price-label {
        height: 25%;
        width: 60%;
        border: 1px solid #7890a3;


    }



}