@mixin back-ground {
  width: 100%;
  height: 100%;
  background-color: white;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  overflow: hidden;
  position: relative;
}

@mixin banner-keyframe($val) {
  @keyframes flyby {
    0% {
      transform: translateX(0);
    }

    50% {
      transform: translateX($val);
    }

    100% {
      transform: translateX(0);
    }
  }
}

@mixin balls($val, $color, $pos) {
  width: $val;
  height: $val;
  border-radius: 50%;
  background-color: $color;
  animation: flyby 5s ease-in-out infinite;
  position: $pos;
}

//THIS IS STYLE FOR MOBILE SCREEN
.bg {
  @include back-ground;

  .blue-ball {
    @include balls(220px, #4467c9, relative);
    //left: 0;
  }

  .red-ball {
    width: 220px;
    height: 220px;
    border-radius: 50%;
    background-color: #ff845f;
    animation: flyby2 5s ease-in-out infinite;
    position: absolute;
    right: 0;
  }
}

.bg2 {
  //display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  text-align: center;
  position: absolute;
  overflow: hidden;

  h1 {
    font-size: 7.4rem;
    font-weight: 600;
    color: white;
    margin: 10px auto;
  }

  span {
    position: relative;
    top: -40px;

    p {
      font-size: 0.9rem;
      font-weight: 600;
      color: white;
    }
  }
}

@keyframes flyby {
  0% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(100px);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes flyby2 {
  0% {
    // opacity: 0;
    transform: translateX(0);
  }

  50% {
    transform: translateX(-100px);
  }

  100% {
    //  opacity: 0;
    transform: translateX(0);
  }
}

