.footer {
    width: 100%;
    height: 100%;
    display: block;
    border-bottom: 1px solid black;
    padding: 0.5rem;
    background-color: #e4f2ee;

    .top-footer {
        width: 100%;
        height: 170px;
        display: flex;
        justify-content: center;
        overflow: hidden;
        flex-wrap: wrap;



        font-size: 0.85rem;

        transition: 0.5s ease-in;
    }

    .top-footer-down {
        width: 100%;
        height: 330px;
        display: flex;
        justify-content: center;
        overflow: hidden;
        flex-wrap: wrap;

        font-size: 0.85rem;

        transition: 0.5s ease-in;
    }

    .top-footer-content {
        width: 100%;
        height: 90%;
        display: flex;
        color: rgb(2, 62, 78);
        overflow: hidden;
        flex-wrap: wrap;
        padding: 0.5rem;
        font-size: 0.85rem;


        ul {
            width: 49%;
            height: 160px;

            cursor: pointer;
            color: rgb(68, 67, 67);
            padding: 5px;
            font-size: 0.6rem;

            & :first-child {
                font-size: 0.9rem;
                position: relative;
                color: rgb(6, 30, 36);


            }

            & :first-child:hover {
                font-size: 0.95rem;
                position: relative;
                color: rgb(6, 30, 36);

                text-decoration: underline;
            }

            & li:hover {
                font-size: 0.65rem;
                position: relative;
                color: rgb(6, 30, 36);

                text-decoration: underline;
            }

        }
    }

    .drop-btn {
        height: 10%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: rgb(3, 37, 46);

        text-align: center;
    }

    .footer-content {
        width: 100%;
        height: 99%;
        display: flex;
        color: rgb(192, 188, 188);
        overflow: hidden;
        flex-wrap: wrap;


        padding: 0.2rem;
        font-size: 0.6rem;

        ul {
            width: 25%;
            height: 20%;

            cursor: pointer;

            padding: 5px;

            & :last-child {
                font-size: 0.55rem;
                position: relative;
                color: rgb(68, 67, 67);


            }

            & :last-child:hover {
                font-size: 0.55rem;
                position: relative;
                color: rgb(92, 91, 91);

                text-decoration: underline;
            }
        }
    }
}



@media only screen and (min-width: 768px) {


    .footer {


        border-bottom: 1px solid black;
        padding: 0.5rem;

        .top-footer {
            width: 100%;
            height: 180px;
            display: flex;
            justify-content: center;
            overflow: hidden;



            font-size: 0.85rem;




        }

        .top-footer-content {
            width: 80%;
            height: 99%;
            display: flex;
            justify-content: space-between;
            color: rgb(2, 62, 78);
            overflow: hidden;
            flex-wrap: wrap;
            padding: 0.5rem;
            font-size: 0.85rem;
          

            ul {
                width: 25%;
                height: 99%;

                cursor: pointer;
                color: rgb(68, 67, 67);
                padding: 5px;
                font-size: 0.6rem;



            }
        }

        .drop-btn {

            display: none;

        }

        .footer-content {
            width: 75%;
            height: 99%;
            border: 1px solid red;


            padding: 0.5rem;
            font-size: 0.85rem;

            ul {
                width: 20%;
                height: 20%;

                cursor: pointer;

                padding: 5px;

                & :last-child {
                    font-size: 0.9rem;
                    position: relative;
                    color: rgb(68, 67, 67);

                    text-decoration: underline;
                }


            }
        }
    }
}

@media only screen and (min-width: 1280px) {





    .footer {
        width: 100%;
        height: 95%;
        margin: 0px auto;
        display: block;
        border-bottom: 1px solid black;
        padding: 0.5rem;

    }

    .top-footer-content {
        width: 25%;
        height: 99%;
        margin: 0px auto;
        color: rgb(192, 188, 188);


        font-size: 0.85rem;

        ul {
            width: 20%;
            height: 20%;

            cursor: pointer;

            padding: 5px;




        }
    }

    .footer-content {
        width: 75%;
        height: 99%;



        padding: 0.5rem;
        font-size: 0.85rem;

        ul {
            width: 20%;
            height: 20%;

            cursor: pointer;

            padding: 5px;

            & :last-child {
                font-size: 0.9rem;
                position: relative;
                color: rgb(68, 67, 67);

                text-decoration: underline;
            }


        }
    }
}